import React from 'react'
import { connect } from "react-redux";
import { addArticle } from './actions/index';
import uuidv1 from "uuid";

function mapDispatchToProps(dispatch) {
  return {
    addArticle: article => dispatch(addArticle(article))
  };
}



class ConnectedCreateTopics extends React.Component {
  constructor() {
    super();
    this.state = {
      articleHeading: ""
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  
  handleSubmit(event) {
    event.preventDefault();
    const { articleHeading } = this.state;
    const _id = uuidv1();
    this.props.addArticle({ articleHeading, _id });
    this.setState({ articleHeading: "" });
  }

  handleChange(event) {
    this.setState({ articleHeading : event.target.value });
    console.log(this.state);
  }

    render() {
        const { articleHeading } = this.state;
        return (
          <form onSubmit={this.handleSubmit}>
            <div className="form-group">
              <label htmlFor="title">Title</label>
              <input
                type="text"
                className="form-control"
                id="title"
                value={articleHeading}
                onChange={this.handleChange}
              />
            </div>
            <button type="submit" className="btn btn-success btn-lg">
              SAVE
            </button>
          </form>
        );
      }
}

const CreateTopics = connect(null, mapDispatchToProps)(ConnectedCreateTopics);
export default CreateTopics